/* src/SpellingBee.css */
body {
    font-family: 'Comic Sans MS', cursive, sans-serif; /* Choose a playful font */
  }
  
  button {
    padding: 10px 20px;
    font-size: 16px;
    margin: 5px;
    cursor: pointer;
    border: none;
    border-radius: 5px;
    background-color: #4caf50; /* Green background color */
    color: white;
    transition: background-color 0.3s ease;
  }
  
  button:hover {
    background-color: #45a049; /* Darker green on hover */
  }
  
  input {
    padding: 10px;
    font-size: 16px;
    margin: 5px;
  }
  
  .hint {
    margin-top: 10px;
    font-style: italic;
  }
  
  .correct-answer {
    color: #4caf50; /* Green for correct answers */
  }
  
  .incorrect-answer {
    color: #f44336; /* Red for incorrect answers */
  }
  